.App{
    width: 100%;
    background-color: black;
    display: flex;
    justify-content: center;
    scroll-behavior: smooth;

    .workarea{
        width: 90%;

        .top{
            width: 100%;

            .top_image{
                overflow: hidden;
        
                img{
                    width: 100%;
                    object-fit: contain;
                }
            }

            .free_div{
                width: 90%;
                position: absolute;
                top: 16vw;
                display: flex;
                justify-content: center;

                .left{
                    flex: 6;
                    display: flex;
                    justify-content: start;

                    h1{
                        font-size: 3vw;
                        color: white;
            
                        span{
                            color: yellow;
                        }
                    }
                }

                .right{
                    flex: 6;
                    display: flex;
                    justify-content: end;

                    .icons{
                        display: flex;
                        flex-direction: column;
                        justify-content: space-evenly;

                        .icon{
                            color: white;
                            font-size: 3vw;
                        }
                    }
                }
            }
        }

        .middle{
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            .perks_heading{
                color: white;
                font-size: 4vw;
                margin-top: 10vw;
                margin-bottom: 7vw;

                span{
                    color: yellow;
                }
            }

        }

        .breakpoint{
            hr{
                color: white;
                margin-top: 7vw;
            }
        }

        .bottom{
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 5vw;
            margin-top: 7vw;

            .left{
                width: 50%;
                margin-right: 2vw;
                
                h3{
                    color: white;
                    display: inline;
                }
                p{
                    color: white;
                    display: inline;
                }
            }

            .right{
                width: 50%;
                height: 300px;
                overflow: hidden;
                border-radius: 7%;
                -webkit-box-shadow: 0px 0px 20px 4px rgba(255,255,255,0.85); 
                box-shadow: 0px 0px 20px 4px rgba(255,255,255,0.85);
                margin-left: 2vw;
            }
        }
    }    
}