.top-bar{
    background-color: black;
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 2vw;

    .left{
        flex: 6;
        display: flex;
        justify-content: start;

        h2{
            color: white;
        }
    }

    .right{
        flex: 6;
        display: flex;
        justify-content: end;
        align-items: center;

        .inner{
            height: 50%;
            display: flex;
            flex-direction: column;
            justify-content: space-around;

            span{
                width: 3.5vw;
                background-color: white;
                height: 3px;
            }
        }
    }
}